<template>
 <div id="app" class="app-container">
    <!-- 1.公共顶部导航条 -->
   <header class="mui-bar mui-bar-nav">
		<h1 class="mui-title">融化心灵</h1>
	</header>
   <!-- 2.容器显示其他组件 -->
   <router-view v-if="isRouterAlive"></router-view>
   <!-- 3.底部tabbar -->
   <mt-tabbar class="hearderbottoms">
      <mt-tab-item id="tab1">
         <router-link to="Home">
            <span class="mui-icon mui-icon-home"></span>
            <span class="mui-tab-label">首页</span>
         </router-link>
      </mt-tab-item>
      <mt-tab-item id="tab2">
          <router-link to="shopCart">
            <span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
            <span class="mui-tab-label">购物车</span>
         </router-link>
      </mt-tab-item>
      <mt-tab-item id="tab3">
         <router-link to="search">
            <span class="mui-icon mui-icon-search"></span>
            <span class="mui-tab-label">搜索</span>
         </router-link>
      </mt-tab-item>
      <mt-tab-item id="tab4">
         <router-link to="personal">
            <span class="mui-icon mui-icon-contact"></span>
            <span class="mui-tab-label">用户</span>
         </router-link>
      </mt-tab-item>
   </mt-tabbar>
 </div>
</template>

<script>
export default {
   name:"App",
   provide(){
      return{
          reload:this.reload
      }
   },
   data(){
      return {
         isRouterAlive:true//刷新
      }
   },
   methods:{
      reload(){
        this.isRouterAlive = false;
        this.$nextTick(function(){
           this.isRouterAlive = true;
        })
     }
   }
}
</script>

<style>
.app-container{
   padding-top:40px;
   padding-bottom:50px;
   overflow-x:hidden;
}
.app-container .mint-tabbar{
   position: fixed;
   width: 100%;
   height: 40px;
   margin-bottom: -5px;
   padding-bottom: 0;
}
.app-container .mint-tabbar a{
   color: #000;
}
</style>



